/* ====== FOOTER ====== */

.footer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	min-height: 60px;
	padding: 20px;
  background-color: #222;
  color: #fff;
}

.footer-content {
	font-size: 10px;
}


/* ====== @MEDIA  >= 768 PX ====== */

@media screen and (min-width: 768px) {
	
	.footer-content {
	font-size: 14px;
	}
	
}



