/* ====== MAP INFO WINDOW ====== */

.info-window {
	width: 170px;
/*	height: 300px;*/
	padding: 10px;
	
}

.info-name {
	font-size: 16px;
	padding-bottom: 5px;
	
}

.info-category,
info-address,
info-link {
		font-size: 12px;
}

.info-category {
	border-top: 1px solid #bebcbc;
	border-bottom: 1px solid #bebcbc;
	padding: 5px;
}

.info-address {
	padding: 5px;
}

.info-link {
	display: block;
	border-top: 1px solid #bebcbc;
	padding: 5px 0 0;
	font-weight: bold;
}


/* ====== @MEDIA  >= 768 PX ====== */

@media screen and (min-width: 768px) {
	
	.info-name {
		font-size: 20px;
	}
	
	.info-category,
	info-address,
	info-link {
		font-size: 15px;
	}
}


