/* ====== HEADER ====== */

.header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
  height: 60px;
	padding-left: 50px;
  background-color: #222;
  color: #fff;
}

.header-menu {
	display: initial;
	position: fixed;
	top: 0;
	left: 0;
	margin: 17px;
	border: none;
	cursor: pointer;
	background-color: transparent;
}

.menu-icon {
  width: 40px;
  height: 5px;
  display: block;
  margin-bottom: 5px;
}

.menu-color {
	background-color: #ddd;
}

.menu-active {
	background-color: #fff;
}

.header-menu:hover path {
	fill: #fff;
}

.header-title {
  font-size: 18px;
}


/* ====== @MEDIA  >= 768 PX ====== */

@media screen and (min-width: 768px) {
	
	.header-menu {
		display: none;
	}
	
	.header-title {
		font-size: 24px;
	}
	
	.is-hidden {
		min-width: 270px;
	}

}



